import "./css/style.css";
import React, { startTransition } from "react";
import { useCallback } from "react";
import { useState } from "react";
import { useReducer } from "react";
import { useEffect } from "react";
import { act } from "react-dom/test-utils";
import { type } from "@testing-library/user-event/dist/type";
import Input from "../Input/Input";
// , useReducer   ?

const reducer = (state, action) => {
  switch (action.type) {
    case "CHANGE_EMAIL":
      return {
        ...state,
        email: action.value,
      };
    case "CHANGE_NICKNAME":
      return {
        ...state,
        nickname: action.value,
      };
    case "CHANGE_PW":
      return {
        ...state,
        pw: action.value,
      };
    case "CHANGE_PWCONFIRM":
      return {
        ...state,
        pwConfirm: action.value,
      };
    default:
      return state;
  }
};
const initialState = {
  email: "",
  nickname: "",
  pw: "",
  pwConfirm: "",
};
const Join = ({ onType }) => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const [isValid, setIsValid] = useState({
    isEmail: false,
    isNickname: false,
    isPw: false,
    isPwConfirm: false,
  });
  const [errMsg, setErrMsg] = useState({
    errEmail: "",
    errNickname: "",
    errPw: "",
    errPwConfirm: "",
  });
  const onChange = useCallback((type, value) => {
    dispatch({
      type,
      value,
    });
  }, []);
  useEffect(() => {
    const regExp =
      /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i;
    if (!regExp.test(state.email)) {
      setIsValid({ ...isValid, isEmail: false });
      setErrMsg({ ...errMsg, errEmail: "   " });
    } else {
      setIsValid({ ...isValid, isEmail: true });
      setErrMsg({ ...errMsg, errEmail: "" });
    }

    return () => {};
  }, [state.email]);
  useEffect(() => {
    if (state.nickname.length < 2) {
      setIsValid({ ...isValid, isNickname: false });
      setErrMsg({ ...errMsg, errNickname: "2  " });
    } else {
      setIsValid({ ...isValid, isNickname: true });
      setErrMsg({ ...errMsg, errNickname: "" });
    }

    return () => {};
  }, [state.nickname]);
  useEffect(() => {
    const regex_pwd =
      /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[@$!%*?&#.~_-])[A-Za-z\d@$!%*?&#.~_-]{8,20}$/;
    if (!regex_pwd.test(state.pw)) {
      setIsValid({ ...isValid, isPw: false });
      setErrMsg({
        ...errMsg,
        errPw: " /  /   8 ",
      });
    } else {
      setIsValid({ ...isValid, isPw: true });
      setErrMsg({ ...errMsg, errPw: "" });
    }

    return () => {};
  }, [state.pw]);

  useEffect(() => {
    if (state.pw !== state.pwConfirm) {
      setIsValid({ ...isValid, isPwConfirm: false });
      setErrMsg({ ...errMsg, errPwConfirm: "  " });
    } else {
      setIsValid({ ...isValid, isPwConfirm: true });
      setErrMsg({ ...errMsg, errPwConfirm: " " });
    }

    return () => {};
  }, [state.pwConfirm]);

  const onJoin = useCallback(() => {
    fetch("http://localhost:4000/register", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        email: state.email,
        nickname: state.nickname,
        password: state.pw,
      }),
    })
      .then((response) => response.json())
      .then((todos) => {
        if (todos.state === false) {
          alert("  .   .");
        } else {
          onType("login");
        }
      })
      .catch((err) => console.log(err));
  }, [state.email, state.nickname, state.pw]);

  return (
    <div className="join-page">
      <Input
        id="email"
        subtitle=""
        placeholder=" "
        value={state.email}
        onChange={(e) => onChange("CHANGE_EMAIL", e.target.value)}
        isValid={isValid.isEmail}
        errMsg={errMsg.errEmail}
        type="email"
      />
      <Input
        id="nickname"
        subtitle=""
        placeholder="   "
        value={state.nickname}
        onChange={(e) => onChange("CHANGE_NICKNAME", e.target.value)}
        isValid={isValid.isNickname}
        errMsg={errMsg.errNickname}
      />
      <Input
        id="pw"
        subtitle=""
        placeholder=" "
        value={state.pw}
        onChange={(e) => onChange("CHANGE_PW", e.target.value)}
        isValid={isValid.isPw}
        errMsg={errMsg.errPw}
        type="password"
      />
      <Input
        id="confirm-pw"
        placeholder=" "
        value={state.pwConfirm}
        onChange={(e) => onChange("CHANGE_PWCONFIRM", e.target.value)}
        isValid={isValid.isPwConfirm}
        errMsg={errMsg.errPwConfirm}
        type="password"
      />
      <button
        className="join-btn"
        type="submit"
        disabled={
          isValid.isEmail &&
          isValid.isNickname &&
          isValid.isPw &&
          isValid.isPwConfirm === true
            ? false
            : true
        }
        onClick={onJoin}
      >
        
      </button>
      <button className="back-btn" onClick={() => onType("login")}>
        
      </button>
    </div>
  );
};

export default Join;
